import "antd/dist/antd.css";
import "./style.css";
import { Form, Input, Button } from "antd";
import { useState } from "react";
import { useHttp } from "../../../../hooks/http.hooks";
import { Link } from "react-router-dom";

const formItemLayout = {
  labelCol: {
    xs: { span: 24 },
    sm: { span: 8 },
  },
  wrapperCol: {
    xs: { span: 24 },
    sm: { span: 16 },
  },
};
const tailFormItemLayout = {
  wrapperCol: {
    xs: {
      span: 24,
      offset: 0,
    },
    sm: {
      span: 16,
      offset: 8,
    },
  },
};

const RegistrationForm = () => {
  const { loading, request } = useHttp();
  const [forms, setForm] = useState({
    email: "",
    password: "",
    name: "",
    surname: "",
    phone: "",
  });

  const changeHandler = (event: {
    target: { name: string; value: string };
  }) => {
    setForm({ ...forms, [event.target.name]: event.target.value });
  };

  const reqisterHandler = async () => {
    try {
      await request("register", "POST", { ...forms });
    } catch (error) {
      console.log(error);
    }
  };

  const [form] = Form.useForm();

  return (
    <Form
      {...formItemLayout}
      form={form}
      name="register"
      className="registration__form"
      initialValues={{
        prefix: "375",
      }}
      scrollToFirstError
    >
      <Form.Item
        name="name"
        label=""
        rules={[
          {
            required: true,
            message: ",   !",
            whitespace: true,
          },
        ]}
      >
        <Input name="name" placeholder=" " onChange={changeHandler} />
      </Form.Item>
      <Form.Item
        name="surname"
        label=""
        rules={[
          {
            message: ",   !",
            whitespace: true,
          },
        ]}
      >
        <Input
          name="surname"
          placeholder=" "
          onChange={changeHandler}
        />
      </Form.Item>
      <Form.Item
        name="email"
        label="E-mail"
        rules={[
          {
            type: "email",
            message: " E-mail!",
          },
          {
            required: true,
            message: " E-mail!",
          },
        ]}
      >
        <Input
          name="email"
          placeholder=" E-mail"
          onChange={changeHandler}
        />
      </Form.Item>

      <Form.Item
        name="password"
        label=""
        rules={[
          {
            required: true,
            message: " !",
          },
        ]}
        hasFeedback
      >
        <Input.Password
          name="password"
          placeholder=" "
          onChange={changeHandler}
        />
      </Form.Item>

      <Form.Item
        name="confirm"
        label=" "
        dependencies={["password"]}
        hasFeedback
        rules={[
          {
            required: true,
            message: " !",
          },
          ({ getFieldValue }) => ({
            validator(_, value) {
              if (!value || getFieldValue("password") === value) {
                return Promise.resolve();
              }
              return Promise.reject(new Error("  !"));
            },
          }),
        ]}
      >
        <Input.Password placeholder=" " />
      </Form.Item>
      <Form.Item
        name="phone"
        label=" "
        rules={[{ message: "  !" }]}
      >
        <Input
          name="phone"
          placeholder="  "
          style={{ width: "100%" }}
          onChange={changeHandler}
        />
      </Form.Item>

      <Form.Item {...tailFormItemLayout}>
        <Link to={"/sign-in"}>
          <Button
            type="primary"
            htmlType="submit"
            className="registration-button"
            onClick={reqisterHandler}
            disabled={loading}
          >
            Register
          </Button>
        </Link>
      </Form.Item>
    </Form>
  );
};

export default RegistrationForm;
